import {
  CloseReportChecklist,
  ProjectReportId,
  ReportText,
  Title,
} from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectCloseReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
  closeReportChecklist: CloseReportChecklist;
}
export interface IUpdateProjectCloseReport extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
  closeReportChecklist?: CloseReportChecklist;
}

export class ProjectCloseReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;
  readonly closeReportChecklist: CloseReportChecklist;

  constructor(args: IProjectCloseReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.closeReportChecklist = args.closeReportChecklist;
  }

  update(args: IUpdateProjectCloseReport): UpdateProjectCloseReport {
    return new UpdateProjectCloseReport({ id: this.id, ...args });
  }
}

export class UpdateProjectCloseReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;
  readonly closeReportChecklist?: CloseReportChecklist;

  constructor(args: IUpdateProjectCloseReport & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.closeReportChecklist = args.closeReportChecklist;
  }
}
